import React from 'react';

class Header extends React.Component {
   render() {
      return (
        <nav className="navbar navbar-transparent navbar-fixed-top navbar-color-on-scroll">
		<div className="container">
			{/*Brand and toggle get grouped for better mobile display*/ }
			<div className="navbar-header">
				<button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#navigation-example">
            		<span className="sr-only">Toggle navigation</span>
		            <span className="icon-bar"></span>
		            <span className="icon-bar"></span>
		            <span className="icon-bar"></span>
        		</button>
				<a className="navbar-brand" href="#0">Sriram</a>
			</div>

			<div className="collapse navbar-collapse" id="navigation-example">
				<ul className="nav navbar-nav navbar-right">
					<li>
						<a href="#profile" id="profile">
    						Profile
    					</a>
					</li>
					<li>
						<a href="#education" id="education">
    						Education
    					</a>
					</li>
					<li>
						<a href="#skillset" id="skillset">
    						Skill set
    					</a>
					</li>
					<li>
						<a href="#contact-us" id="contact-us">
    						Contact
    					</a>
					</li>
				</ul>
			</div>
		</div>
	</nav>
      );
   }
}
export default Header;