import React from 'react';

class Body extends React.Component {

    render() {

        return (
            <div className="wrapper">
                <div className="header header-filter" id="particles-js" style={{ backgroundImage: this.props.data.profileImage }}></div>

                <div class="main main-raised">
                    <div class="profile-content">
                        <div class="container">
                            <div class="row">
                                <div class="profile profile_section">
                                    <div class="avatar">
                                        <img src="/assets/img/profile/profile.jpg" alt="profile" class="img-circle img-responsive img-raised" />
                                    </div>
                                    <div class="name">
                                        <h3 class="title">Sriram</h3>
                                        <h6>Web developer</h6>
                                    </div>
                                </div>
                                <div class="description text-center">
                                    <p>Hi, i am well wisher in design overall architecture of the web application. Maintain quality and ensure responsiveness
                                    of applications,Collaborate with the rest of the engineering team to design and launch new features. Maintain code
                                    integrity and organization. Experience working with graphic designers and converting designs to visual elements. Understanding
                                    and implementation of security and data protection, Highly experienced with back-end programming languages, Development
							experience for both mobile and desktop and Knowledge of code versioning tools.</p>
                                </div>
                                <div class="row education_section">
                                    <div class="col-md-12">
                                        <div class="profile-tabs ">
                                            <div class="profile">
                                                <h3 class="title">Education</h3>
                                            </div>
                                            <section id="cd-timeline" class="cd-container">
                                                <div class="cd-timeline-block">
                                                    <div class="cd-timeline-img cd-picture">
                                                        <img src="/assets/img/graduvate.svg" alt="MCA" />
                                                    </div>
                                                    <div class="cd-timeline-content">
                                                        <h3>MCA Master of Computer Applications</h3>
                                                        <p>M.Kumarasamy Engineering College, Karur</p>
                                                        <span class="cd-date">2014</span>
                                                    </div>
                                                </div>
                                                <div class="cd-timeline-block">
                                                    <div class="cd-timeline-img cd-movie">
                                                        <img src="assets/img/graduatecrowd.svg" alt="Bsc" />
                                                    </div>
                                                    <div class="cd-timeline-content">
                                                        <h3>BSC (CS) Bachelor of Computer Science</h3>
                                                        <p>VHNSN College, Virudhunagar</p>
                                                        <span class="cd-date">2011</span>
                                                    </div>
                                                </div>
                                            </section>
                                        </div>
                                    </div>
                                    <div class="col-md-12 skillset_section">
                                        <div class="profile-tabs ">
                                            <div class="profile">
                                                <h3 class="title">Skill set</h3>
                                            </div>
                                            <section id="skill_set_temp">
                                            </section>
                                            <section id="skill_set">
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>Laravel</h4>
                                                    <div id="laravel_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>Php</h4>
                                                    <div id="php_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>CakePHP</h4>
                                                    <div id="cake_php_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>MySql</h4>
                                                    <div id="mysql_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>Angular js</h4>
                                                    <div id="angular_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>Jquery</h4>
                                                    <div id="jquery_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>Javascript</h4>
                                                    <div id="javascript_container"></div>
                                                </div>
                                                <div class="col-sm-3 col-xs-6 profile">
                                                    <h4>HTML/CSS</h4>
                                                    <div id="html_css_container"></div>
                                                </div>
                                            </section>
                                        </div>

                                    </div>
                                 
                                    <div class="col-md-12 contact">
                                        <div class="profile-tabs ">
                                            <div class="profile">
                                                <h3 class="title">Contact</h3>
                                            </div>
                                            <section class="map-responsive">
                                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3885.5517851461695!2d80.15170621450926!3d13.127556490753662!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3a52630621137f9b%3A0xbdf2fae8064c21cd!2sOragadam!5e0!3m2!1sen!2sin!4v1521948269377" width="1200" height="300" frameborder="0" style={{border:0}} allowfullscreen title="google-map"></iframe>
                                            </section>
                                            <form id="fupForm" name="contact_form">
                                                <div class="col-sm-8 col-md-offset-2">
                                                    <div class="alert alert-success" ng-if="p.enableSuccess">
                                                        <div class="container-fluid">
                                                            <div class="alert-icon">
                                                                <i class="material-icons">check</i>
                                                            </div>
                                                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                                                <span aria-hidden="true"><i class="material-icons">clear</i></span>
                                                            </button>
                                                            <b>Success </b> Your message has sent! Thank you :)
									</div>
                                                    </div>
                                                    <div class="col-sm-6">
                                                        <div class="form-group">
                                                            <input type="text" value="" required name="first_name" placeholder="First name" class="form-control" />
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-6">
                                                        <div class="form-group">
                                                            <input type="text" value="" name="last_name" placeholder="Last name" class="form-control" />
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-6">
                                                        <div class="form-group">
                                                            <input type="email" value="" required name="email" placeholder="Email" class="form-control" />
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-6">
                                                        <div class="form-group">
                                                            <input type="text" value="" name="company" placeholder="Company" class="form-control" />
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-12">
                                                        <div class="form-group">
                                                            <textarea class="form-control" required name="message" placeholder="Message" rows="4"></textarea>
                                                        </div>
                                                    </div>
                                                    <div class="row">
                                                        <div class="col-sm-12">
                                                            <input type="submit" value="Send" class="btn btn-success submitBtn right-align" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </form>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}
export default Body;